.wrapper {
  padding-top: 50px;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.jumbotron {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  display: block;
  background-color: rgba(240, 240, 240, 0.7);
  padding: 20px;
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 160px;
  margin: 1rem;
  position: relative;
  width: 160px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;
}

.card > .card-img {
  height: 100%;
  width: 100%;
}


.navbar {
  width: 100%;
}

.nav {
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: rgba(240, 240, 240, 0.7);
  padding: 10px;
}

.nav-item {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

* {
  /* box-sizing: border-box; */
  font-family: sans-serif;
}

html,
body,
#root {
  padding: 0;
  margin: 0;
  height: 100%;
}

body {
  background-image: url(/clicky-game/static/media/background.1f8c06e7.jpg);
  background-size: cover;
}

